footer {
    background: #00181d;
    position: relative;
    height: 200px;
    border-top: 2px white solid;

    .scroll-to-to-wrapper {
        height: 100px;
    }

    .scroll-to-top{
        text-align: center;
        color: white;
        background: var(--pink);
        width: 55px;
        height: 55px;
        font-size: 34px;
        top: -28px;
        position: relative;
        cursor: pointer;
        transition: all 0.5s;
        border-radius: 40px;
        border: 2px white solid;

        span {
            vertical-align: -.185rem !important;
        }



        &:hover {
            background: var(--brightCyan);
            color: var(--pink);
        }
    }

    .icon {
        background: #002229;
        cursor: pointer;
        height: 55px;
        width: 55px;
        position: relative;
        text-align: center;
        color: var(--pink);
        font-size: 24px;
        transition: all 0.5s;
        margin: 10px 10px;
        border-radius: 40px;
        border: 2px var(--pink) solid;

        &:hover {
            color: var(--brightCyan);
            border-color: var(--brightCyan);
        }
    }

    .footnote-wrapper {
        margin-top: 25px;
        width: 100%;
    }

    .footnote-text {
        color: #969696;
        text-align: center;
        font-size: 12px;
    }
}